
@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');

/* YOUR ORIGINAL LAYOUT */
.loginSignup {
  width: 100%;
  min-height: 80vh;
  background: var(--color-background);
  padding: 100px 0 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loginsignup-container {
  width: 580px;
  max-width: 90%;
  height: auto;
  min-height: 600px;
  background: var(--white);
  margin: auto;
  padding: 40px 60px;
  box-sizing: border-box;
}

.loginsignup-fields {
  display: flex;
  flex-direction: column;
  gap: 29px;
  margin-top: 30px;
}

.loginsignup-fields input {
  height: 72px;
  width: 100%;
  padding-left: 20px;
  border: 1px solid #c9c9c9;
  outline: none;
  color: var(--color-secondary-text);
  font-size: 18px;
  box-sizing: border-box;
}

.loginsignup-container button {
  width: 100%;
  max-width: 480px;
  height: 72px;
  color: var(--white);
  background: var(--btn-secondary);
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}

.loginsignup-container button:hover {
  background: var(--btn-secondary-hover);
}

.loginsignup-login {
  margin-top: 20px;
  color: var(--color-secondary-text);
  font-size: 18px;
  font-weight: 500;
  text-align: center;
}

.loginsignup-login a {
  color: var(--primary-green);
  text-decoration: none;
}

.loginsignup-login a:hover {
  text-decoration: underline;
}

/* ANIMATED FORM CSS */
.wrapper {
  overflow: hidden;
  border-radius: 15px;
}

.wrapper .title-text {
  display: flex;
  width: 200%;
}

.wrapper .title {
  width: 50%;
  font-size: 35px;
  font-weight: 600;
  text-align: center;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .slide-controls {
  position: relative;
  display: flex;
  height: 50px;
  width: 100%;
  overflow: hidden;
  margin: 30px 0 10px 0;
  justify-content: space-between;
  border: 1px solid var(--medium-gray);
  border-radius: 15px;
}

.slide-controls .slide {
  height: 100%;
  width: 100%;
  color: var(--primary-green);
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  line-height: 48px;
  cursor: pointer;
  z-index: 1;
  transition: all 0.6s ease;
}

.slide-controls label.signup {
  color: var(--text-dark);
}

.slide-controls .slider-tab {
  position: absolute;
  height: 100%;
  width: 50%;
  left: 0;
  z-index: 0;
  border-radius: 15px;
  background: linear-gradient(to right, var(--primary-green), var(--primary-green-dark));
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

input[type="radio"] {
  display: none;
}

#signup:checked ~ .slider-tab {
  left: 50%;
}

#signup:checked ~ label.signup {
  color: var(--white);
  cursor: default;
  user-select: none;
}

#signup:checked ~ label.login {
  color: var(--text-dark);
}

#login:checked ~ label.signup {
  color: var(--text-dark);
}

#login:checked ~ label.login {
  color: var(--white);
  cursor: default;
  user-select: none;
}

.wrapper .form-container {
  width: 100%;
  overflow: hidden;
}

.form-container .form-inner {
  display: flex;
  width: 200%;
}

.form-container .form-inner form {
  width: 50%;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.form-inner form .field {
  height: 72px;
  width: 100%;
  margin-top: 0;
}

.form-inner form .field input {
  height: 100%;
  width: 100%;
  outline: none;
  padding-left: 20px;
  border-radius: 15px;
  border: 1px solid #c9c9c9;
  border-bottom-width: 2px;
  font-size: 18px;
  transition: all 0.3s ease;
  background: var(--white);
}

.form-inner form .field input:focus {
  border-color: var(--primary-green);
}

.form-inner form .field input::placeholder {
  color: var(--dark-gray);
}

form .field input:focus::placeholder {
  color: var(--primary-green);
}

.form-inner form .pass-link {
  margin-top: 5px;
  font-size: 16px;
  color: var(--text-medium);
}

form .btn {
  height: 72px;
  width: 100%;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
  margin-top: 30px;
}

form .btn .btn-layer {
  height: 100%;
  width: 300%;
  position: absolute;
  left: -100%;
  background: linear-gradient(to right, var(--primary-green), var(--primary-green-dark));
  border-radius: 15px;
  transition: all 0.4s ease;
}

form .btn:hover .btn-layer {
  left: 0;
}

form .btn input[type="submit"] {
  height: 100%;
  width: 100%;
  z-index: 1;
  position: relative;
  background: none;
  border: none;
  color: var(--white);
  padding-left: 0;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}